import React from "react";
import * as d3 from "d3";
// import * as d3 from "https://cdn.skypack.dev/d3@7";

export const Gantt = () =>{ 
  const svgRef = React.useRef(null);


  const taskArray = [
    {
      medicamento: "Atropina",
      via: "Intravenoso",
      dosis: "50mg Frasco Amp.",
      regimen:"Cada 12 horas",
      disolucion: "SF 20ml",
      startTime: "2021-2-2", //year/month/day
      endTime: "2021-2-4"
    },

    {
      medicamento: "Hidrocortizona",
      via: "Intravenoso",
      dosis: "50mg Frasco Amp.",
      regimen:"Cada 8 horas",
      disolucion: "SF 20ml",
      startTime: "2021-2-1", //year/month/day
      endTime: "2021-2-5"
    },

    {
      medicamento: "Lansoprazol",
      via: "Intravenoso",
      dosis: "30mg Frasco Amp.",
      regimen:"Cada 24 horas",
      disolucion: "-----",
      startTime: "2021-2-5", //year/month/day
      endTime: "2021-2-8"
    },

    {
      medicamento: "Amiodarona Clorhidrato",
      via: "Oral",
      dosis: "200mg Comprimido",
      regimen:"Cada 12 horas",
      disolucion: "Iniciar al teminal BIC",
      startTime: "2021-2-2", //year/month/day
      endTime: "2021-2-4"
    },

    {
      medicamento: "Omeprazol ",
      via: "Oral",
      dosis: "20mg CÃ¡psula",
      regimen:"Cada 24 horas",
      disolucion: "-----",
      startTime: "2021-2-1", //year/month/day
      endTime: "2021-2-6"
    },

    {
      medicamento: "Fenoterol",
      via: "Nebulizacion",
      dosis: "20ml Frasco Amp.",
      regimen:"Cada 4 horas",
      disolucion: "----",
      startTime: "2021-2-4", //year/month/day
      endTime: "2021-2-8"
    },

    {
      medicamento: "Salbutamol",
      via: "Inhalatorio",
      dosis: "0.5% Frasco",
      regimen:"Cada 8 horas",
      disolucion: "SOS",
      startTime: "2021-2-6", //year/month/day
      endTime: "2021-2-8"
    },

  ];

  const w = 800 ;
  const h = taskArray.length*(20+24)  ;

React.useEffect( ()=> {
 

  const tickScale = d3.scaleLinear()
        .domain([1, 7])
        .range([122, 270]);


  const svgEl = d3.select(svgRef.current);
  svgEl.selectAll("*").remove(); // Clear svg content before adding new elements
  const svg = svgEl
      .append("g")
      .attr("width", "100%")
      .attr("height","100%")
      .attr("class", "svg")
      .attr("overflow","scroll");

  svg.append("rect")
    .attr( "width" , w )
    .attr( "height", tickScale( taskArray.length ) + 43 )
    .attr("rx", 4)
    .attr("ry", 4)
    .attr( "fill", "#E1DEE9")
    .attr( "opacity","0.59");


  const dateFormat =  d3.timeParse("%Y-%m-%d");

  const timeScale = d3.scaleTime()
        .domain([d3.min(taskArray, function(d) {return dateFormat(d.startTime);}),
                d3.max(taskArray, function(d) {return dateFormat(d.endTime);})])
        .range([0,w-150]);


  let categories = [];

  for (let i = 0; i < taskArray.length; i++){
    categories.push( taskArray[i].via );
  }

  let catsUnfiltered = categories; //for vert labels

  categories = checkUnique(categories);


  makeGant(taskArray, w, h);

  // Titulo
  svg.append("text")
      .text("Diagrama de medicaciÃ³n")
      .attr("x", w/2)
      .attr("y", 50)
      .attr("text-anchor", "middle")
      .attr("font-size", 20)
      .attr("fill", "#2F4858")
      .attr("opacity","0.8");
              
  function makeGant(tasks, pageWidth, pageHeight) {

      const barHeight = 20 ;
      const gap = barHeight +5;
      const topPadding = 70;
      const sidePadding = 125;
      const leftPadding = 20;

      const colorScale = d3.scaleLinear()
        .domain([0, categories.length])
        .range(["#00B9FA", "#F95002"])
        .interpolate(d3.interpolateHcl);

      makeGrid(sidePadding, topPadding, pageWidth, pageHeight);
      drawRects(tasks, gap, topPadding, leftPadding, sidePadding, barHeight, colorScale, pageWidth, pageHeight);
      vertLabels(gap, topPadding, sidePadding, barHeight, colorScale);

  }


  function drawRects(theArray, theGap, theTopPad, leftPadding, theSidePad, theBarHeight, theColorScale, w, h){

    svg.append("g")
        .attr("id","rect_vias")
        .selectAll("rect")
        .data(theArray)
        .enter()
        .append("rect")
        .attr("x", leftPadding )
        .attr("y", function(d, i){
            return i*theGap + theTopPad - 2;
        })
        .attr("width", function(d){
          return w-theSidePad/3  ;
        })
        .attr("height", theGap)
        .attr("stroke", "none")
        .attr("fill", function(d){
          for (let i = 0; i < categories.length; i++){
              if (d.via === categories[i]){
                return d3.rgb(theColorScale(i));
              }
          }
        })
        .attr("opacity", 0.2);


      const rectangles = svg.append('g')
      .attr("id","medicamentos")
      .selectAll("rect")
      .data(theArray)
      .enter();
      


      const innerRects = rectangles.append("rect")
            .attr("rx", 3)
            .attr("ry", 3)
            .attr("x", function(d){
              return timeScale(dateFormat(d.startTime)) + theSidePad ;
              })
            .attr("y", function(d, i){
                return i*theGap + theTopPad;
            })
            .attr("width", function(d){
                return (timeScale(dateFormat(d.endTime))-timeScale(dateFormat(d.startTime)));
            })
            .attr("height", theBarHeight)
            .attr("stroke", "none")
            .attr("fill", function(d){
              for (let i = 0; i < categories.length; i++){
                  if (d.via === categories[i]){
                    return d3.rgb(theColorScale(i));
                  }
              }
            })
  

      const rectText = rectangles.append("text")
            .text( (d) =>  d.medicamento)
            .attr("x", (d)=>(timeScale(dateFormat(d.endTime))-timeScale(dateFormat(d.startTime)))/2 + timeScale(dateFormat(d.startTime)) + theSidePad)
            .attr("y", (d, i)=> i*theGap + 14+ theTopPad)
            .attr("font-size", 14)
            .attr("text-anchor", "middle")
            .attr("text-height", theBarHeight)
            .attr("fill", "#fff")
            .attr("cursor","pointer")
            // cursor="pointer" pointer-events="all"


      rectText.on('mouseover', function(e) {
      //  console.log(this.x.animVal.getItem(this));
              let tag = "";

        if (d3.select(this).data()[0].medicamento !== undefined){
          tag = "Medicamento: " + d3.select(this).data()[0].medicamento + "<br/>" + 
                "Via: " + d3.select(this).data()[0].via + "<br/>" + 
                "dosis: " + d3.select(this).data()[0].dosis + "<br/>" + 
                "Regimen: " + d3.select(this).data()[0].regimen + "<br/>" + 
                "Starts: " + d3.select(this).data()[0].startTime + "<br/>" + 
                "Ends: " + d3.select(this).data()[0].endTime + "<br/>" + 
                "Disolucion/Otro: " + d3.select(this).data()[0].disolucion;
        } else {
          tag = "Medicamento: " + d3.select(this).data()[0].medicamento + "<br/>" + 
                "Via: " + d3.select(this).data()[0].via + "<br/>" + 
                "dosis: " + d3.select(this).data()[0].dosis + "<br/>" + 
                "Regimen: " + d3.select(this).data()[0].regimen + "<br/>" + 
                "Starts: " + d3.select(this).data()[0].startTime + "<br/>" + 
                "Ends: " + d3.select(this).data()[0].endTime + "<br/>" + 
                "Disolucion/Otro: " + d3.select(this).data()[0].disolucion;
        }
          
          const output = document.getElementById("tag");
          const x = this.x.animVal.getItem(this) + "px";
          const y = this.y.animVal.getItem(this) + 25 + "px";

        output.innerHTML = tag;
        output.style.top = y ;
        output.style.left = x ;
        output.style.display = "block";
      }).on('mouseout', function() {
        const output = document.getElementById("tag");
        output.style.display = "none";
            });


      innerRects.on('mouseover', function(e) {
      // console.log(this);
            let tag = "";

            if (d3.select(this).data()[0].medicamento !== undefined){
              tag = "Medicamento: " + d3.select(this).data()[0].medicamento + "<br/>" + 
              "Via: " + d3.select(this).data()[0].via + "<br/>" + 
              "dosis: " + d3.select(this).data()[0].dosis + "<br/>" + 
              "Regimen: " + d3.select(this).data()[0].regimen + "<br/>" + 
              "Starts: " + d3.select(this).data()[0].startTime + "<br/>" + 
              "Ends: " + d3.select(this).data()[0].endTime + "<br/>" + 
              "Disolucion/Otro: " + d3.select(this).data()[0].disolucion;
            } else {
              tag = "Medicamento: " + d3.select(this).data()[0].medicamento + "<br/>" + 
              "Via: " + d3.select(this).data()[0].via + "<br/>" + 
              "dosis: " + d3.select(this).data()[0].dosis + "<br/>" + 
              "Regimen: " + d3.select(this).data()[0].regimen + "<br/>" + 
              "Starts: " + d3.select(this).data()[0].startTime + "<br/>" + 
              "Ends: " + d3.select(this).data()[0].endTime + "<br/>" + 
              "Disolucion/Otro: " + d3.select(this).data()[0].disolucion;
            }
            const output = document.getElementById("tag");

            const x = (this.x.animVal.value + this.width.animVal.value/2 +200) + "px";
            const y = this.y.animVal.value + 25 + "px";

            output.innerHTML = tag;
            output.style.top = y;
            output.style.left = x  ;
            output.style.display = "block";
          }).on('mouseout', function() {
                const output = document.getElementById("tag");
                output.style.display = "none";

              });

  }


  function makeGrid(theSidePad, theTopPad, w, h){

    // grid
    svg.append("g")
    .attr('class', 'grid')
    .attr('transform', 'translate(' + theSidePad + ', '  + tickScale( taskArray.length )+ ')')
    .call(d3.axisBottom(timeScale).tickSize([ theTopPad - tickScale( taskArray.length )]))
    .selectAll("text")  
                .style("text-anchor", "middle")
                .attr("fill", "#000")
                .attr("stroke", "none")
                .attr("font-size", 11)
                .attr("dy", "1em")
                .attr('transform','rotate(45 0 0)');
    
  }

  function vertLabels(theGap, theTopPad, theSidePad, theBarHeight, theColorScale){
      let numOccurances = [] ;
      let prevGap = 0;

      for (let i = 0; i < categories.length; i++){
        numOccurances[i] = [categories[i], getCount(categories[i], catsUnfiltered)];
      }
      // axisText
    svg.append("g") //without doing this, impossible to put grid lines behind text
      .attr("id","Vias")
      .selectAll("text")
      .data(numOccurances)
      .enter()
      .append("text")
      .text(function(d){
        return d[0];
      })
      .attr("x", 35 )
      .attr("y", function(d, i){
        if (i > 0){
            for (let j = 0; j < i; j++){
              prevGap += numOccurances[i-1][1];
            // console.log(prevGap);
              return d[1]*theGap/2 + prevGap*theGap + theTopPad;
            }
        } else{
        return d[1]*theGap/2 + theTopPad;
        }
      })
      .attr("font-size", 14)
      .attr("text-anchor", "start")
      .attr("text-height", 14)
      .attr("fill", function(d){
        for (let i = 0; i < categories.length; i++){
            if (d[0] === categories[i]){
            //  console.log("true!");
              return d3.rgb(theColorScale(i)).darker();
            }
        }
      });

  }

  //from this stackexchange question: http://stackoverflow.com/questions/1890203/unique-for-arrays-in-javascript
  function checkUnique(arr) {
    let hash = {}, result = [];
    for ( let i = 0, l = arr.length; i < l; ++i ) {
        if ( !hash.hasOwnProperty(arr[i]) ) { //it works with objects! in FF, at least
            hash[ arr[i] ] = true;
            result.push(arr[i]);
        }
    }
    return result;
  }

  //from this stackexchange question: http://stackoverflow.com/questions/14227981/count-how-many-strings-in-an-array-have-duplicates-in-the-same-array
  function getCounts(arr) {
    let i = arr.length, // let to loop over
        obj = {}; // obj to store results
    while (i) obj[arr[--i]] = (obj[arr[i]] || 0) + 1; // count occurrences
    return obj;
  }

  // get specific from everything
  function getCount(word, arr) {
    return getCounts(arr)[word] || 0;
  }

},[taskArray,h,w]) ;
  return <svg ref={svgRef} width={w} height={h} />;
}
